import React, { useState, useEffect } from "react"
import SEO from "../components/seo"
import ProjectLayout from "../components/projectLayout"

import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

const units = ["\u03B1RD", "\u03B2RD", "\u03B3RD", "RPU"]
const newsreel = [
  "Republica Independent Reporter",
  "The New Republicar",
  "RNN - Republica News Network",
  "People's National Media Conglomerate of Republica",
]
const automachineANames = ["recycled printer"]

function ProjectRepublica() {
  const [currDollars, setCurrDollars] = useState(0)
  const [totalDollars, setTotalDollars] = useState(0)
  const [numAutoMachineA, setNumAutoMachineA] = useState(0)
  const [gameStage, setGameStage] = useState(0)

  //Updates auto-printers etc
  /*
  useEffect(() => {
    setInterval(() => {
      setCurrDollars(currDollars => currDollars + numAutoMachineA)
      setTotalDollars(totalDollars => totalDollars + numAutoMachineA)
    }, 1000)
  })
  */

  //bound to main button
  function printDollar() {
    const multiplier = 1
    setCurrDollars(currDollars => currDollars + 1 * multiplier)
    setTotalDollars(totalDollars => totalDollars + 1 * multiplier)
  }

  //automachine A logic
  function purchaseAutoMachineA(price) {
    if (currDollars >= price) {
      setCurrDollars(currDollars => currDollars - price)
      setNumAutoMachineA(numAutoMachineA => numAutoMachineA + 1)
    } else {
      alert("Insufficient funds")
    }
  }
  function AutoMachineAComponent() {
    const price = Math.round(10 * 1.2 ** numAutoMachineA)
    return (
      <>
        <p>
          Number of {automachineANames[gameStage]}: {numAutoMachineA}
        </p>
        <p>
          ${price} per {automachineANames[gameStage]}
        </p>
        <button
          onClick={() => purchaseAutoMachineA(price)}
          disabled={currDollars < price}
        >
          Purchase {automachineANames[0]}
        </button>
      </>
    )
  }
  function iterateAutoMachineA() {
    setCurrDollars(currDollars => currDollars + numAutoMachineA)
    setTotalDollars(totalDollars => totalDollars + numAutoMachineA)
  }

  return (
    <ProjectLayout>
      <SEO title="Republica Game" />
      <Row>
        <h3>
          National coffers: {currDollars} {units[gameStage]}
        </h3>
        <Col sm={2}>
          <button onClick={() => printDollar()}>
            Print {units[gameStage]}
          </button>
        </Col>
        <hr />
      </Row>
      <Row>
        <Col sm={9}>
          <h4>Industry</h4>
          {totalDollars > 10 && <AutoMachineAComponent />}
        </Col>
        <Col sm={3}>
          <h4>{newsreel[gameStage]}</h4>
        </Col>
      </Row>
      <button onClick={() => iterateAutoMachineA()}>
        Iterate Automachine A
      </button>
    </ProjectLayout>
  )
}

export default ProjectRepublica
